.loader-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100vw;
    height:50vh;
    background-color: var(--black);
    color:var(--blue);
    position:fixed;
    z-index:100;
    overflow-y: hidden;
    overflow-x:hidden;
    left:12%;
    top:25%;
}